= fileDisplayer.displayItems(rootDiv.value!, viewModel.studio.items,\n  {\n    displayModels: false, cb: { select: item => selected = item },\n    additionalRoot: { externalData: app, id: app.ID, name: app.name },\n  });\n});\n\nonUnmounted(() => {\n  treeView.reset();\n});\n\nasync function move() {\n  await executor.execute(async () => {\n    if (!selected) { return; }\n    let target: Partial<IMoveTargetRequired>;\n    switch (selected.externalData.objectType) {\n      case \"model\": throw new Error(\"unexpected target\");\n      case \"folder\":\n        const folder = selected.externalData;\n        target = {\n          address: { name: props.itemToBeMoved.name, path: folder.path + \"/\" + folder.name },\n          owner: folder.owner.ID != props.itemToBeMoved.owner.ID ? { id: folder.owner.ID, type: folder.owner.objectType } : undefined,\n        };\n        break;\n      case \"module\":\n        const module = selected.externalData;\n        target = { owner: { id: module.ID, type: \"module\" }, address: { path: \"/\", name: \"\" } };\n        break;\n      case \"application\":\n        const application = selected.externalData;\n        target = { owner: { id: application.ID, type: \"application\" }, address: { path: \"/\", name: \"\" } };\n        break;\n    }\n    await moveUseCase.moveItem(props.itemToBeMoved, target);\n    emit(\"close\");\n  }, { loading: true });\n}\n\n\n\n</script> \n\n<template>\n  <div>\n    <div id=\"content-area\" class=\"popover__content\">\n      <div ref=\"rootDiv\" style=\"height: 100%\" class=\"errorDiv\"></div>\n      <button @click=\"move()\"></button>\n    </div>\n  </div>\n</template>\n\n\n<style scoped>\n* {\n  font-family: \"Roboto\";\n}\n\nul {\n  list-style-type: none;\n}\n\n.move-to-target .mdi {\n  font-size: 24px;\n  margin-right: 10px;\n}\n</style>\n\n\n \n\n<style scoped>\n.move-to-target {\n  margin: 0;\n  color: #151233;\n  font-weight: 400;\n  font-size: 14px;\n  letter-spacing: 0px;\n  cursor: pointer;\n 